@import url(https://www.testdome.com/content/source/stylesheets/embed.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #191919;
  background-image: url(/static/media/logoDaniel.4fb92dec.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  color: rgba(255, 255, 255, 0.8);
}

section {
  margin-bottom: 2rem;
}

header {
  background-color:rgba(0,0,0,0.8);
}

footer {
  background-color: rgba(0,0,0,0.8);
}

.App-link {
  color: #61dafb;
}

.jumbotron {
  background-color: rgba(0,0,0,0.5);
  background-image: url(/static/media/hero.eaca6ddc.jpg);
  background-size: cover;
  background-position: 60% center;
  overflow: hidden;
  padding: 0;
}

.jumbotron video {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.jumbotron .container {
  position: relative;
  z-index: 2;
  height: 50vh;
  padding: 0;
}

.row {
  min-height: 100%;
}

.hero-col1 { 
  position: relative;
}

.hero-col1 p {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 0%;
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
}

.intro {
  background-color: rgba(0,0,0,0.5);
}

.games {
  position: relative;
  overflow: hidden;
  background-color: rgba(0,0,0,1);
  z-index: 0;
}

.gamesBG {
  background-repeat: no-repeat;
	background-size: cover;
  background-position: center;
	position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  z-index: -1;
}

.gamesContent {
  z-index: 1;
}

.game-description {
  background-color: rgba(0,0,0,0.5);
  border-radius: 10px;
}

.shadow-black {
  text-shadow: 2px 2px 2px rgba(0,0,0,0.8);
}

.certificate {
  border-radius: 8px;
}
